import { Dashboard } from "@mui/icons-material";
import DateRangeIcon from '@mui/icons-material/DateRange';
import AboutUsIcon from '@mui/icons-material/Groups';
import HomeIcon from '@mui/icons-material/Home';
import PersonIcon from '@mui/icons-material/Person';
import LogoutIcon from '@mui/icons-material/Logout';
import { Drawer, IconButton, MenuItem, Typography } from '@mui/material';
import Box from '@mui/material/Box';
import { Link } from 'react-router-dom';
import * as Cores from '../../util/cores';
import DashboardIcon from '@mui/icons-material/Dashboard';

export default function MenuMobile(props) {

    const { isLoggedIn, setExibirModal, showMenu, handleCloseMenu, setShowMenu, isMobile } = props

    return (
        <Box sx={{
            background: 'white', display: 'flex', borderTopLeftRadius: '25px', borderTopRightRadius: '25px',
            boxShadow: '0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)'
        }}>
            <Link to='/' style={{ textDecoration: 'none', color: Cores.COR_PRINCIPAL }}>
                <MenuItem onClick={handleCloseMenu} sx={{ display: 'grid' }}>
                    <IconButton component="label" sx={{ padding: '0px' }}>
                        <HomeIcon fontSize='small' sx={{fill: Cores.COR_PRINCIPAL}}/>
                    </IconButton>
                    <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 25%)' }} variant='caption' >Home</Typography>
                </MenuItem>
            </Link>
            <Link to='/dashboard' style={{ textDecoration: 'none', color: Cores.COR_PRINCIPAL }}>
                <MenuItem onClick={handleCloseMenu} sx={{ display: isLoggedIn ? 'flex' : 'none', display: 'grid' }}>
                    <IconButton color="secondary" component="label" sx={{ padding: '0px' }}>
                        <DashboardIcon fontSize='small' sx={{fill: Cores.COR_PRINCIPAL}}/>
                    </IconButton>
                    <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 33%)' }} variant='caption'>Minha Área</Typography>
                </MenuItem>
            </Link>

            <Link to='/agendamento' style={{ textDecoration: 'none', color: Cores.COR_PRINCIPAL }}>
                <MenuItem onClick={handleCloseMenu} sx={{ display: 'grid' }}>
                    <IconButton color="secondary" component="label" sx={{ padding: '0px' }}>
                        <DateRangeIcon fontSize='small' sx={{fill: Cores.COR_PRINCIPAL}}/>
                    </IconButton>
                    <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 33%)' }} variant='caption'>Agendamento</Typography>
                </MenuItem>
            </Link>

            <Link to='/perfil' style={{ textDecoration: 'none', color: Cores.COR_PRINCIPAL }}>
                <MenuItem onClick={handleCloseMenu} sx={{ display: isLoggedIn ? 'none' : 'flex', display: 'grid' }}>
                    <IconButton color="secondary" component="label" sx={{ padding: '0px' }}>
                        <PersonIcon fontSize='small' sx={{fill: Cores.COR_PRINCIPAL}} />
                    </IconButton>
                    <Typography sx={{ fontWeight: 700, transform: 'translate(0px, 25%)' }} variant='caption'>Conta</Typography>
                </MenuItem>
            </Link>
        </Box>
    )
}