import { Box, Typography } from "@mui/material";
import Utils from "../../util/Utils";
import constantes from "../../util/constantes";
import Login from "./Login";
import CadastroForm from "./cadastro/CadastroForm";
import { useState } from 'react'
import { useCommon } from "../../components/hooks/useCommon";

export default function LoginWrapper(props) {
    const { setIsLoggedIn, isLoggedIn, setUserDetails, userDetails } = props
    const { height, width } = Utils.useWindowDimensions();
    const [isCadastro, setIsCadastro] = useState(false)
    const { alterarLoad, notificar, Common, alterarMensagemLoad } = useCommon()

    const comecarCadastro = () => {
        setIsCadastro(true)
    }

    const sairCadastro = () => {
        setIsCadastro(false)
    }
    return (
        <Box sx={{ height: '100%' }}>
            {userDetails.id !== 0 ?
                <Typography>Usuário já logado!</Typography>
                :
                <Box>
                    {!isCadastro ?
                        <Login
                            comecarCadastro={comecarCadastro}
                            isMobile={width < constantes.MOBILE_SCREEN_SIZE}
                            setIsLoggedIn={setIsLoggedIn}
                            setUserDetails={setUserDetails}
                            alterarLoad={alterarLoad}
                            notificar={notificar}
                            Common={Common}
                            alterarMensagemLoad={alterarMensagemLoad}
                        />
                        :
                        <Box sx={{paddingTop: '64px'}}>
                            <CadastroForm
                                sairCadastro={sairCadastro}
                                alterarLoad={alterarLoad}
                                notificar={notificar}
                            />
                        </Box>
                    }
                </Box>
            }
        </Box>
    )
}