import { Box } from "@mui/material";
import Utils from '../../util/Utils';
import SelecionarData from "./agenda/calendario/SelecionarData";
import InformacoesDia from "./agenda/informacoes/InformacoesDia";
import { useState } from 'react'
import InformacoesDiaDrawer from "./agenda/informacoes/InformacoesDiaDrawer";
import DataSelecionadaHeader from "./agenda/calendario/DataSelecionadaHeader";
import * as Constantes from '../../util/constantes'
import Agendamentos from "./agenda/agendamentos/Agendamentos";

export default function AgendaWrapper(props) {
    const { isMobile } = props
    const [dataSelecionada, setDataSelecionada] = useState(null)
    const [horaSelecionada, setHoraSelecionada] = useState(null)
    const [drawerInformacoes, setDrawerInformacoes] = useState(false)
    const [calendarioAgendamento, setCalendarioAgendamento] = useState(Constantes.CALENDARIO)

    const abrirDrawerInformacoes = (e, valor) => {
        e.preventDefault()
        e.stopPropagation()
        setHoraSelecionada(valor)
        setDrawerInformacoes(true)
    }
    const fecharDrawerInformacoes = () => {
        setDrawerInformacoes(false)
    }

    const alterarCalendarioAgendamento = (value) => {
        setCalendarioAgendamento(value)
    }

    return (
        <Box id='agenda-wrapper' sx={{
            width: '100%',
            height: '100%',
            background: 'white',
            borderTopRightRadius: '10px',
            borderTopLeftRadius: '10px',
            backdropFilter: 'blur(25px)',
            ...Utils.barra(), justifyContent: isMobile ? 'left' : 'center'
        }}>

            <Box sx={{ height: '5%' }}>
                <DataSelecionadaHeader
                    alterarCalendarioAgendamento={alterarCalendarioAgendamento}
                    calendarioAgendamento={calendarioAgendamento}
                />
            </Box>
            {calendarioAgendamento === Constantes.CALENDARIO ?
                <Box sx={{ height: '95%' }}>
                    <Box sx={{ height: '54%' }}>
                        <SelecionarData
                            isMobile={isMobile}
                            setDataSelecionada={setDataSelecionada}
                            dataSelecionada={dataSelecionada}
                            alterarCalendarioAgendamento={alterarCalendarioAgendamento}
                            calendarioAgendamento={calendarioAgendamento}
                        />
                    </Box>
                    <Box sx={{ height: '46%', overflowY: 'scroll' }}>
                        <InformacoesDia
                            horaSelecionada={horaSelecionada}
                            dataSelecionada={dataSelecionada}
                            abrirDrawerInformacoes={abrirDrawerInformacoes}
                        />
                    </Box>

                    <InformacoesDiaDrawer
                        setAnchor={setDrawerInformacoes}
                        isMobile={isMobile}
                        anchor={drawerInformacoes}
                    />
                </Box>
                :
                <Box>
                    <Agendamentos />
                </Box>
            }
        </ Box>
    )
}